:root{
    --cl1:#fff;
    --cl2:rgb(63, 63, 63);
    --cl3:rgb(241, 241, 241);
    --cl4:linear-gradient(90deg,rgb(187, 208, 217),rgb(143, 165, 176));
    --cl5:linear-gradient(90deg,rgb(165, 191, 177),rgb(141, 165, 152));
    --cl6:rgba(92, 99, 110, 0.541);
    --cl7:rgb(134, 135, 136);
}
body,html{
    margin:0;
    padding:0;
    width:100%;
    height:100%;
}
body{
    display: flex;
    justify-content: center;
}
h1{
    color: var(--cl2);
    font-family:Cairo;
    line-height:20px;
}
h2{
    color: var(--cl2);
    font-family:Cairo;
    line-height:15px;
}
.wrap1{
    margin:10%;
    height: 60%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.input1{
    font-size:18px;
    width:90%;
    height:30px;
    border: none;
    border-bottom:1px solid var(--cl2);
    outline: none;
}
.input2{
    font-size:12px;
    width:90%;
    height:20px;
    border: none;
    background:var(--cl3);
    border-radius:20px;
    outline: none;
    padding:5px 15px;
}
.btn1{
    font-size:16px;
    font-family: Cairo;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: space-around;
    width:fit-content;
    padding: 0px 20px;
    height:30px;
    border:1px solid var(--cl2);
    border-radius:12px;
    transition:0.1s;
}
.btn1:hover{
    background: var(--cl2);
    color: var(--cl1);
}
.nav1{
    margin:10%;
    height: 20%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}

.profile1x1{
    width:40px;
    height:40px;
    border-radius:40px;
    object-fit:cover;
    object-position:50%;
}
.vert1{
    margin-left:5%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}
h3{
    font-family: Cairo;
    color: var(--cl2);
    margin:0;
    line-height:20px;
}

.message{
    font-family:Cairo;
    line-height:20px;
    color: var(--cl2);
    border-radius:5px 5px 0px 5px;
    max-width:250px;
    padding:2%;
    margin:1% 2%;
}
.send{
    background:var(--cl4);
    align-self: flex-end;
}
.recieved{
    background:var(--cl5);
    align-self: flex-start;
}
.nav2{
    flex:2;
    width:91%;
    margin:2%;
    padding:0.1% 0;
    border-radius:20px 20px 20px 0px;
    display: flex;
    background:var(--cl3);
    align-items: center;
    padding-left:5%;
}
.nav2 >*{
    margin:0 2%;
}
.online{
    background: rgb(37, 134, 17);
}
.offline{
    background: rgb(199, 8, 8);
}
.messages{
    flex:17;
    width:95%;
    margin:2%;
    overflow:scroll;
    scrollbar-width:none;
    display: flex;
    flex-direction: column;
}

.image1x1{
    width:100%;
    border-radius:3px;
    height:auto;
}
.input3{
    font-size:15px;
    width:90%;
    height:30px;
    border: none;
    margin:5px;
    border-radius:20px;
    border:1px solid var(--cl2);
    background: none;

    outline: none;
    padding:5px 15px;
}

.btn2{
    font-size:20px;
    font-family: Cairo;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: space-around;
    width:50px;
    height:40px;
    border-radius:30px;
    border:1px solid var(--cl2);
    color:var(--cl2);
    margin:3px;
}
.btn2:hover{
    background: var(--cl2);
    color: #fff;
}
.link{
    color: rgb(13, 98, 155);
    font-family:Cairo;
}
.error{
    width:100%;
    color:rgb(218, 55, 55);
    font-family: Cairo;
}

.container{
    width:1100px;
    height:100vh;

    display:flex;
    align-items: center;
    justify-content: center;
    background:var(--cl1);
    
}
.sidePanel{
    width:30%;
    height:80%;
    margin:10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    /* background: #d4238a; */
}
.login_container{
    width:40%;
    max-width:400px;
    height:50%;
    border-radius:25px;
    border:1px solid var(--cl2);
    background:var(--cl1);
    user-select: none;
}
.contactContainer{
    width:100%;
    height:88%;
    border-radius:25px;
    border:1px solid var(--cl2);
    background:var(--cl1);
    user-select: none;
}
.userInfo{
    width:100%;
    height:10%;
    border-radius:25px;
    border:1px solid var(--cl2);
    background:var(--cl1);
    user-select: none;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
.chatContainer{
    width:50%;
    height:80%;
    border-radius:25px;
    border:1px solid var(--cl2);
    background:var(--cl1);
    user-select: none;
    overflow: hidden;
    display: flex;
    flex-direction: column;
}

.listItems{
    margin:10%;
    height: 60%;
    display: flex;
    flex-direction: column;
    justify-content:flex-start;
}
.ContactListItem{
    height: 50px;
    width:100%;
    margin-top:3%;
    border-radius:12px;
    display: flex;
    padding:0px 5px;
    justify-content:flex-start;
    align-items: center;
}
.ContactListItem:hover{
    background: var(--cl3);
}
.chatInput{
    flex:1.5;
    width:100%;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index:1;
}
.fileInput{
    z-index:0;
    background:var(--cl6);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius:25px;
    width:inherit;
    height:inherit;
    position:absolute;
}
.symbol{
    width:200px;
    height:200px;
    opacity:0.5;
}
.imagePreview1{
    max-width:400px;
    max-height:400px;
}

.imagePreview2{
    z-index:10;
    width:100vw;
    height:100vh;
    top:0px;
    left:0px;
    position: fixed;
    background:var(--cl6);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.imagePreview2 img{
    margin:20px;
    max-width:60vw;
    max-height:90vh;
}
.settingsContainer{
    width:50%;
    height:80%;
    border-radius:25px;
    border:1px solid var(--cl2);
    background:var(--cl1);
    user-select: none;
    overflow: scroll;
    display: flex;
    justify-content:center;

}
.wrap3{
    width:88%;
    display:flex;
    flex-direction: column;
    align-items: center;
    
}
.wrap4{
    width:80%;
    display:flex;
    flex-direction:row;
    padding:10px 20px;
    margin: 10px 0px;
    border:1px solid var(--cl6);
    border-radius:25px;
    flex-wrap: wrap;
}
.wrap4 >*{
    margin:6px 0;
}
.wrap4 h3{
    width: 100%;
}
.wrap4 .btn1{
    width:fit-content;
    margin-right:20px;
}

h4{
    font-family:Cairo;
    color:var(--cl7);
    font-size:24px;
    margin:0;
}
.btn3{
    transform: scale(0.8);
    font-family:Cairo;
    margin-left:10px;
    color: var(--cl6);
}
.btn3:hover{
    color: var(--cl2);
}
@keyframes pop {
    0%{transform: scaleX(0.96);opacity:0.5;}
    100%{transform: scale(1);opacity:1;} 
}
* {
    animation: pop 0.5s;
}
.profile2x2{
    width:200px;
    height:200px;
    border-radius:100px;
    object-fit:cover;
    object-position:50%;
    margin-right:30px;
    margin-top:30px;
    margin-bottom:20px;
}
h5{
    margin:0;
    font-family: Cairo;
    line-height:50px;
    font-size:40px;
    color: var(--cl2);
    display: flex;
}
h6{
    margin:0;
    font-family: Cairo;
    line-height:30px;
    font-size:20px;
    color: var(--cl7);
    display: flex;
}
.btn4{
    font-family: Cairo;
    font-size:18px;
    color: rgb(77, 146, 224);
}

.un{
    font-size: 14px;
    color: var(--cl7);
    font-family: Cairo;
    line-height:14px;
}
.image1{
    width:100%;
    height:100%;
    object-fit:contain;
}